import React from 'react'
import { Chrono } from "react-chrono";
import data from "../../assets/data";

const Cv = () => {
  return (
    <div style={{ width: "100%", height: "100vh" }}>
      <Chrono 
        items={data} 
        theme={{primary: "blue", secondary: "lightgray" }}
        mode="VERTICAL"
        slideShow
        slideItemDuration={4000}
      />
    </div>
  );
};

export default Cv